<template>
  <v-main class="d-flex justify-center">
    <v-img
      lazy-src="../assets/road.jpg"
      src="../assets/road.jpg"
      height="100vh"
      class="ms-0 ps-0 bg"
      eager
    >
      <v-img
        class="mx-auto mt-15"
        max-width="300"
        src="../assets/logo2.png"
        alt="logo"
      />
      <v-card
        width="500"
        shaped
        dark
        class="mx-auto mt-10"
        color="#00000090"
      >
        <v-card-title
          class="ms-1 text-h4"
          @click="count++, validationClick()"
        >
          <strong>Login.</strong>
        </v-card-title>

        <form>
          <v-card-text>
            <v-text-field
              v-model="login.RUA"
              label="CPF"
              prepend-icon="mdi-account-circle"
              outlined
              maxlength="11"
              oninput="this.value = this.value.replace(/[^0-9]/g, '')"
            />
            <v-text-field
              v-model="login.FAMILIA"
              label="Senha"
              :type="showPassword ? 'text' : 'password'"
              prepend-icon="mdi-lock"
              outlined
              hide-details="true"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="showPassword = !showPassword"
            />
          </v-card-text>

          <v-divider />

          <v-card-actions class="pb-5">
            <a
              href="#"
              target=""
              small
              text
              class="
                text-decoration-none
                white--text
                text-uppercase text-overline
                ml-5
              "
            >
              Esqueceu a senha?
            </a>

            <v-spacer />

            <v-btn
              v-if="loginBtn"
              to="/home"
              color="blue"
              width="100"
              class="mr-5"
              @click="logIn"
            >
              Login
            </v-btn>
          </v-card-actions>
        </form>
      </v-card>
    </v-img>
  </v-main>
</template>

<script>
  export default {
    name: 'LoginScreen',

    data: () => ({
      showPassword: false,
      logado: '',
      loginBtn: false,
      count: 0,
      users: [],
      companies: [],
      login: {
        ID: '',
        EMPRESAID: '',
        PERFIL: '',
        COR: '',
        NOMES: '',
        FAMILIA: '',
        RUA: '',
        EMAIL: '',
        CEP: '',
        NUMERO: '',
        SOBRE: '',
        IMAGE: '',
        RAZAOSOCIAL: '',
        IDEMPRESA: '',
        EMPRESACNPJ: '',
        EMPRESACPF: '',
        EMPRESACEP: '',
        EMPRESANUM: '',
        CONTATO1: '',
        TIPO: '',
      },
    }),

    created () {
      this.logOut()
      this.login = {}
      this.reloadOnce()
    },

    methods: {
      logOut () {
        if (window.localStorage) {
          if (!localStorage.getItem('logado')) {
            localStorage.removeItem('id')
            localStorage.removeItem('profile')
            localStorage.removeItem('user')
            localStorage.removeItem('sobrenome')
            localStorage.removeItem('email')
            localStorage.removeItem('cep')
            localStorage.removeItem('numero')
            localStorage.removeItem('sobre')
            localStorage.removeItem('rua')
            localStorage.removeItem('img')
            localStorage.removeItem('empid')
            localStorage.removeItem('razaosocial')
            localStorage.removeItem('empcnpj')
            localStorage.removeItem('empcpf')
            localStorage.removeItem('empcep')
            localStorage.removeItem('empnum')
            localStorage.removeItem('empcontato')
            localStorage.removeItem('emptipo')
          } else {
            window.localStorage.logado = this.logado
          }
        }
      },

      logIn () {
        window.localStorage.id = this.login.ID
        window.localStorage.profile = this.login.PERFIL
        window.localStorage.user = this.login.COR
        window.localStorage.sobrenome = this.login.NOMES
        window.localStorage.email = this.login.EMAIL
        window.localStorage.cep = this.login.CEP
        window.localStorage.numero = this.login.NUMERO
        window.localStorage.sobre = this.login.SOBRE
        window.localStorage.rua = this.login.RUA
        window.localStorage.img = this.login.IMAGE
        window.localStorage.empid = this.login.EMPRESAID
        window.localStorage.razaosocial = this.login.RAZAOSOCIAL
        window.localStorage.empcnpj = this.login.CNPJ
        window.localStorage.empcep = this.login.EMPRESACEP
        window.localStorage.empnum = this.login.EMPRESANUM
        window.localStorage.empcontato = this.login.CONTATO1
        window.localStorage.emptipo = this.login.TIPO
        window.localStorage.reload = true
        this.logado = true
        window.localStorage.logado = this.logado
      },

      reloadOnce () {
        if (window.localStorage) {
          if (!localStorage.getItem('reload')) {
            localStorage.reload = true
            window.location.reload()
          } else {
            localStorage.removeItem('reload')
          }
        }
      },

      fetchUsers () {
        return fetch('https://sheetdb.io/api/v1/khm5ici88y1u4')
          .then((r) => r.json())
          .then((r) => {
            this.users = r
          })
      },
      fetchCompanies () {
        return fetch('https://sheetdb.io/api/v1/8nmjdq4kt9z0x')
          .then((res) => res.json())
          .then((res) => {
            this.companies = res
          })
      },
      validateRua () {
        return this.users.some((el) => el.RUA === this.login.RUA)
      },
      ruaId () {
        if (this.validateRua()) {
          try {
            return this.users.find((x) => x.RUA === this.login.RUA).ID
          } catch (err) {
            console.log('erro')
          }
        } else {
          return ''
        }
      },
      pswdId () {
        if (this.login.FAMILIA) {
          try {
            return this.users.find((x) => x.FAMILIA === this.login.FAMILIA).ID
          } catch (err) {
            console.log('erro')
          }
        } else {
          return ''
        }
      },
      matchingPswd () {
        if (
          this.ruaId() !== '' &&
          this.pswdId() !== '' &&
          this.ruaId() === this.pswdId()
        ) {
          return true
        } else {
          return false
        }
      },
      userInfo () {
        if (this.matchingPswd()) {
          const ID = this.users.find((x) => x.RUA === this.login.RUA).ID
          const PERFIL = this.users.find((x) => x.RUA === this.login.RUA).PERFIL
          const COR = this.users.find((x) => x.RUA === this.login.RUA).COR
          const NOMES = this.users.find((x) => x.RUA === this.login.RUA).NOMES
          const FAMILIA = this.users.find((x) => x.RUA === this.login.RUA).FAMILIA
          const RUA = this.users.find((x) => x.RUA === this.login.RUA).RUA
          const EMAIL = this.users.find((x) => x.RUA === this.login.RUA).EMAIL
          const CEP = this.users.find((x) => x.RUA === this.login.RUA).CEP
          const NUMERO = this.users.find((x) => x.RUA === this.login.RUA).NUMERO
          const SOBRE = this.users.find((x) => x.RUA === this.login.RUA).SOBRE
          const IMAGE = this.users.find((x) => x.RUA === this.login.RUA).IMAGE
          const EMPRESAID = this.users.find((x) => x.RUA === this.login.RUA).EMPRESAID
          const RAZAOSOCIAL = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).RAZAOSOCIAL
          const CNPJ = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CNPJ
          const EMPRESACEP = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CEP
          const EMPRESANUM = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).NUM
          const CONTATO1 = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).CONTATO1
          const TIPO = this.companies.find((x) => x.IDEMPRESA === EMPRESAID).TIPO
          return { ID, PERFIL, COR, NOMES, FAMILIA, RUA, EMAIL, CEP, NUMERO, SOBRE, IMAGE, EMPRESAID, RAZAOSOCIAL, CNPJ, EMPRESACEP, EMPRESANUM, CONTATO1, TIPO }
        } else {
          return ''
        }
      },
      validateLogin () {
        if (this.matchingPswd() === true) {
          this.login = this.userInfo()
          this.loginBtn = true
        } else {
          localStorage.removeItem('logado')
        }
      },
      validationClick () {
        if (this.count >= 5) {
          this.fetchUsers()
          this.fetchCompanies()
          this.validateLogin()
        } else {
          localStorage.removeItem('logado')
        }
      },
    },
  }
</script>

<style scoped>
  .bg {
    position: fixed;
    left: 0;
    top: 0
  }
</style>
